.delete-products-container {
  width: 300px;
  border: 1px solid black;
  background-color: white;
}
.product-delete-tag {
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px;
  border: 2px solid black;
  border-radius: 10px;
}
.delete-buttons {
  border-radius: 10px;
  border: none;
  height: 35px;
  background-color: rgba(0, 95, 177, 0.255);
}
#delete-product-tag {
  text-align: center;
}
.delete-container{
    margin-top: 2%;
    display: flex;
    justify-content: center;
}