body {
  background-image: url("../../../public/pictures/background.png");
}
.buttons_container {
  background-color: rgba(255, 255, 255, 0.627);
  width: 300px;
  height: 300px;
  border: white solid 2px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
}
.buttons_home_page {
  height: 50px;
  width: 100px;
  margin: 10px;
  border: none;
  background-color: white;
  border-radius: 10px;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 70vh;
}
.btn {
  color: white;
  height: 60px;
  width: 120px;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.625);
}
