.form_container{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
}
.sign_up_container {

  display: flex;
  width: 250px;
  height: 350px;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgba(255, 255, 255, 0.763);
  border-radius: 10px;
}
.inp_sign_up{
    margin: 5px;
    height: 30px;
    text-align: center;
    border: none;
    border-radius: 10px;
}
#sign_up_button{
    width: 100px;
    height:30px ;
    margin-top: 10px;
    margin-left:30%;
    background-color: white;
    border: none;
    border-radius: 10px;
}
#sign_up_tag{
    text-align: center;
    color: white;
}
.error{
    text-align: center;
}