.payment_container {
  margin-top: 5%;
  width: 300px;
  border: 2px solid white;
  border-radius: 10px;
}
#pay_btn {
  height: 35px;
  width: 100px;
  border: none;
  background-color: white;
  border-radius: 10px;
}
#payment_tag {
  text-align: center;
  color: white;
}
#total {
  color: white;
  background-color: black;
  text-align: center;
}
.payment_products {
  display: flex;
  justify-content: space-around;
  background-color: white;
  margin: 10px;
  height: 40px;
  border-radius: 10px;
}
.container {
  display: flex;
  justify-content: center;
}

.pay_btn_container{
display: flex;
justify-content: center;
}
