.form_container {
  display: flex;
  align-self: center;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
}
.sign_in_container {
  width: 300px;
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background-color: rgba(244, 244, 244, 0.702);
  border-radius: 10px;
}
.inp {
  margin: 10px;
  border: none;
  border-radius: 10px;
  height: 30px;
  text-align: center;
}
#sign_in_btn {
  background-color: rgba(255, 255, 255, 0.583);
  border: 1px solid black;
  border-radius: 10px;
  height: 35px;
}
#tag{
  text-align: center;
  color: white;
}
#errorDetails{
  text-align: center;
  text-decoration: underline;
}