.container {
  border-radius: 10px;
  display: flex;
  justify-content: center;
  height: 80%;
  position: absolute;
  background-color: rgb(255, 255, 255);
  width: 200px;
}

.container2 {
  display: flex;
}
.category_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  list-style: none;
  width: 250px;
  text-align: center;
  align-items: center;
}
.li_category {
  color: black;
  font-size: 22px;
  text-decoration: underline;
  margin: 2px;
}

@media screen and (max-width: 768px) {
  .container {
    border-radius: 10px;
    display: flex;
    height: 100%;
    position: absolute;
    background-color: rgb(255, 255, 255);
    width: 100%;
  }
}
