.products {
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 10px;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 10px;
  font-weight: bold;
  width: 270px;
}
.product_img {
  height: 100px;
  border-radius: 10px;
}
.addBtn {
  border: none;
  background-color: rgb(0, 136, 239);
  border-radius: 10px;
  width: 90px;
  height: 20px;
  color: white;
}

@media screen and (max-width : 768px) {
  .products{
    width: 150px;
  }
}
